:root {
    --swiper-theme-color: #007aff
}

.fb-slider-content-wrapper {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: clamp(300px, 60vh, 500px);
    justify-content: center;
    .fb-slider-content-inner {
        box-sizing: border-box;
        margin: 0 auto;
        max-width: 80%;
        padding: clamp(15px, 6vw, 30px);
        text-align: center;
        width: 100%;
    }
}

.swiper-wrapper {
    height: auto !important;
}

.fb-swiper {
	position: relative;
	overflow: hidden;
}

.fb-slider-container{
	position: relative;
	width: 100%;
	min-width: 0;
	transition: box-shadow 0.2s ease;

	.swiper-notification {
		left: 0;
		opacity: 0;
		pointer-events: none;
		position: absolute;
		top: 0;
		z-index: -1000;
	}

	.swiper-button-next.swiper-button-disabled,
	.swiper-button-prev.swiper-button-disabled {
		pointer-events: all;
	}

	.swiper-pagination {
		bottom: 0 !important;
		&.swiper-pagination-bullets {
			max-width: 100%;
		}
	}

	.swiper-button-prev,
	.swiper-button-next {
		border-style: none;
	}
}

:where(.wp-site-blocks *:focus) {
    outline-width: 0 !important;
}